.salonHeader{
	position: relative;
	background: url('/assets/images/headline_bg.jpg') center center/auto 100% no-repeat;
	height: 60px;
	display: flex;
	align-items: center;
	margin-bottom: 15px;
	.rankIcon{
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		height: 90px;
