		// 	height: 90px;
		// }
		// &.others{
		// 	height: 65px;
		// 	margin-left: 15px;
		// 	margin-top: -10px;
		// }
	}
	.salonName{
		// font-family: "Midashi Go MB31", $baseFont;
		font-size: 24px;
		font-weight: bold;
		padding-left: 110px;
		color: #fff;
		text-shadow: 3px 3px 1px rgba(212, 53, 129, 0.9);
		line-height: 1.1;

