	.ctaBlock{
		margin: 0 auto;
		max-width: 335px;
		.numberOfPeople{
			position: relative;
			border: 1px solid #8fc320;
			background-color: #eefdca;
			border-radius: 5px;
			padding: 5px 15px 10px 15px;
			color: #8fc320;
			font-weight: bold;
			text-align: center;
			&::before{
			  content: "";
			  position: absolute;
			  bottom: -16px;
			  left: 25%;
			  margin-left: 2px;
			  border: 8px solid transparent;
			  border-top: 8px solid #eefdca;
			  z-index: 2;
			}

			&::after{
			  content: "";
			  position: absolute;
			  bottom: -20px;
			  left: 25%;
			  border: 10px solid transparent;
			  border-top: 10px solid #8fc320;
			  z-index: 1;
			}

