					justify-content: center;
					align-items: center;
					text-align: center;
					border: 1px solid #f25aa4;
					box-shadow: 1px 1px 0 2px rgba(0,0,0,0.2);
					padding: 13px 5px;
					font-size: 0.95rem;
					line-height: 1.2;
