.recommend{
	margin-bottom: 30px;
	article.entry{
		border: 1px solid #ccc;
		padding: 5px;
	}
	.viewMore{
		text-align: center;
		position: relative;
		margin-top: 15px;
		a{
			display: inline-block;
			padding: 5px 10px;
			background-color: #fff;
			// font-weight: bold;
			position: relative;
			z-index: 2;
		}
		&::after{
			content: '';
			position: absolute;
			top: 50%;
			left: 0;
			width: 100%;
			height: 1px;
			background-color: #ededed;
			z-index: 1;
		}
	}
	@media screen and (min-width: $breakpoint + 1){
		@at-root{
			.salonBody{
				.recommend{
					max-width: 90%;
				}
			}
		}
		margin-bottom: 50px;

		article.entry{
			padding: 10px 20px 10px 10px;
			margin-bottom: 20px;
		}
		.viewMore{
			margin-top: 30px;
		}
	}

}
/*---------------------------------
サロン詳細
---------------------------------*/
.salonNavi{
	margin: 10px 0;
	padding: 0 15px;
	ul{
		display: flex;
		li{
			flex: 1;
			padding: 0 2px;
			a,span{
				display: block;
				padding: 5px 5px;
				text-align: center;
				// background-color: $keyColor;
				// color: #fff;
				font-size: 0.9rem;
				font-weight: bold;
				line-height: 1.2;
				color: $keyColor;
				border: 1px solid $keyColor;
			}
			span{
				background-color: #ffecf3;
				// color: #fff;
				// opacity: 0.6;
			}
		}
	}
	@media screen and (min-width: $breakpoint + 1){
		padding: 0;
		margin: 0 0 15px 0;
		ul{
			li{
				a{
					@include hover;
				}
			}
		}
	}
}

#salonDetail{
	.salonHeader{
		.salonName{
			// font-family: "Midashi Go MB31", $baseFont;
			padding-left: 15px;
			line-height: 1.1;
		}
		@media screen and (min-width: $breakpoint + 1){
			.salonName{
				padding-left: 20px;
			}
		}
	}

	.ctaBlock{
		@media screen and (max-width: $breakpoint){
			ul.imgBtn{
				li{
					img{
						height: 45px;
					}
				}
			}
		}
		@media screen and (min-width: $breakpoint + 1){
			width: 515px;
		}
	}
}

.commentsArea{
	// padding: 0 10px;
	margin: 20px 0;
	a{
		color: $linkColor;
	}

	@media screen and (min-width: $breakpoint + 1){
		max-width: 90%;
		margin: 30px 0;
	}
}

